let burg = document.querySelector('.js-burger');

let menu = document.querySelector('.js-header-menu');

let searchButton = document.querySelector('.headerActions__btn_search');



function showMobileMenu () {

    if (!burg) return false



    burg.addEventListener('click', function(){



        if(burg.classList.contains('is-open')){

            burg.classList.remove('is-open');



            page.classList.remove('is-bg');

            body.classList.remove('is-hidden');

            menu.classList.remove('show');

        }else{

            burg.classList.add('is-open');

            menu.classList.add('show');



            page.classList.add('is-bg');

            body.classList.add('is-hidden')

        }

    })

}







document.addEventListener('click', (e) => {



    if(menu.classList.contains('show')){

        const withinBoundaries = e.composedPath().includes(menu);

        const withinBurg = e.composedPath().includes(burg);

        const withinSearch = e.composedPath().includes(searchButton);



        if (!withinBoundaries && !withinBurg && !withinSearch) {

            menu.classList.remove('show');

            burg.classList.remove('is-open');



            page.classList.remove('is-bg');

            body.classList.remove('is-hidden');

        }

    }



    return false

})
let headerSearch = document.querySelector('.header__search');
let headerSearchClose = document.querySelector('.header__search-close');
let headerSearchInput = document.querySelector('.searchForm__input');
let headerSearchResult = document.querySelector('.searchResult');
let searchBtn = document.querySelector('.headerActions__btn_search');
let page = document.querySelector('.page');
let body = document.querySelector('body');

window.addEventListener('load', function () {
    
    searchBtn.addEventListener('click', function(){

        if(menu.classList.contains('show')){
            menu.classList.remove('show');
            burg.classList.remove('is-open');
        }

        headerSearch.classList.add('is-open');
        page.classList.add('is-bg');
        body.classList.add('is-hidden')
    })

    headerSearchClose.addEventListener('click', function(){
        headerSearch.classList.remove('is-open');
        page.classList.remove('is-bg');
        body.classList.remove('is-hidden')
    })

    headerSearchInput.addEventListener('input', function(){
        if(headerSearchInput.value.length > 3){
            headerSearchResult.classList.add('is-open')
        }else{
            headerSearchResult.classList.remove('is-open')
        }
    })
})

function headerFixed() {
  let body = document.querySelector('body')
  let header = document.querySelector('header');
  let firstScreen = document.querySelector('.js-first-screen')
  let comaprePage = document.querySelector('.compare')

  if(comaprePage) return false

  let headerHeight = header.offsetHeight;

  if (firstScreen){
    let firstScreenHeight = firstScreen.offsetHeight;
    let sumOffset = headerHeight + firstScreenHeight

    if (window.pageYOffset > sumOffset) {
      header.classList.add("fixed");
      body.style.paddingTop = headerHeight + 'px'
    } else {
      header.classList.remove("fixed");
      body.style.paddingTop = ''
    }
  }else{
    if (window.pageYOffset > headerHeight) {
      header.classList.add("fixed");
      body.style.paddingTop = headerHeight + 'px'
    } else {
      header.classList.remove("fixed");
      body.style.paddingTop = ''
    }
  }
}

window.addEventListener('scroll', function () {
  headerFixed();
})
const breadcrumbs = () => {
    const selector = '.js-breadcrumbs';
    const breadcrumbs = document.querySelector(selector);

    if (!breadcrumbs) return;

    const list = breadcrumbs.querySelector('[data-list]');
    const inside = breadcrumbs.querySelector('[data-inside]');
    const items = list && list.querySelectorAll('[data-item]');
    const toggle = list && list.querySelector('[data-item="toggle"]');

    if (!items || !inside || !items[0]) {
        breadcrumbs.classList.add('all-hide');
        return;
    }

    const params = {};

    items.forEach(item => {
        const itemWidth = Math.ceil(item.getBoundingClientRect().width);
        const type = item.getAttribute('data-item');

        item.setAttribute('data-width', itemWidth);

        if (params[type] !== undefined) {
            params[type] += itemWidth;
        } else {
            params[type] = itemWidth;
        }
    });

    checkWidth();

    toggle && toggle.addEventListener('click', toggleInside);

    projectOptions.onClick.push(target => {
        !target.closest(selector) && toggleInside(null, 'hide');
    });

    window.addEventListener('resize', throttle(checkWidth, 150));

    function toggleInside(e, action) {
        if (inside.classList.contains('show') || action === 'hide') {
            toggleVisibility(inside, 'hide');
            inside.classList.remove('show');
        } else {
            toggleVisibility(inside, 'show');
            inside.classList.add('show');
        }
    }

    function checkWidth() {
        const listWidth = Math.ceil(list.getBoundingClientRect().width);

        let available = listWidth - params.main - params.current;
        let viewDots = false;

        if (available < params.link) {
            available -= params.dots;
            viewDots = true;
        }

        let viewLinks = 0;

        for (let i = items.length - 1; i >= 0; i--) {
            const item = items[i];
            const type = item.getAttribute('data-item');

            if (type === 'dots') {
                if (viewDots) {
                    item.classList.remove('hide');
                    inside.classList.remove('hide');
                } else {
                    item.classList.add('hide');
                    inside.classList.add('hide');
                }
            }

            if (type === 'link') {
                const index = item.getAttribute('data-index');
                const itemWidth = item.getAttribute('data-width');
                const clone = inside.querySelector('[data-item="' + index + '"]');

                if (itemWidth < available) {
                    viewLinks++;
                    available -= itemWidth;

                    item.classList.remove('hide');
                    if (clone) clone.classList.add('hide');
                } else {
                    item.classList.add('hide');
                    if (clone) clone.classList.remove('hide');
                }
            }
        }

        if (viewLinks < 1) {
            breadcrumbs.classList.add('all-hide');
        } else {
            breadcrumbs.classList.remove('all-hide');
        }
    }
}

const bindCallbackForm = (formNode) => {
    if (!formNode) {
        return false;
    }
    for (const phoneInput of formNode.querySelectorAll('input[type="tel"]')) {
        if (!!Inputmask) {
            Inputmask({"mask": ['+7 999 999-99-99', '8 999 999-99-99'], showMaskOnHover: true,}).mask(phoneInput);
        }
    }

    const messageNode = formNode.querySelector('.form__text');
    const initialMessage = messageNode.innerHTML;

    formNode.addEventListener('submit', (e) => {
        e.preventDefault();
        formNode.querySelector('[type="submit"]').setAttribute('disabled', 'disabled')
        fetch(formNode.getAttribute('action'), {
            method: "POST",
            body: new FormData(formNode),
        }).then((resp) => resp.json())
            .then(({success}) => {
                if (success) {

                    formNode.reset();
                    showMessageModal('В ближайшее время с Вами свяжется менеджер для уточнения всех деталей.');
                    setTimeout(() => {
                        formNode.querySelector('[type="submit"]').removeAttribute('disabled');
                    }, 10000)

                } else {
                    messageNode.innerHTML = 'Не удалось отправить заявку. Попробуйте ещё раз, или свяжитесь с нами по телефону <a href="tel:+74959882028">+7(495) 988-20-28</a>.';

                    setTimeout(() => {
                        messageNode.innerHTML = initialMessage;
                    }, 15000)
                }
            }).catch(() => {
            messageNode.innerHTML = 'Не удалось отправить заявку. Попробуйте ещё раз, или свяжитесь с нами по телефону <a href="tel:+74959882028">+7(495) 988-20-28</a>.';

            setTimeout(() => {
                messageNode.innerHTML = initialMessage;
            }, 15000)
        })
    })

}
window.addEventListener('DOMContentLoaded', () => {
    bindCallbackForm(document.querySelector('.form_callcback'))
})
